.nfd-onboarding-drawer__toggle {
    align-items: center;
    background: var(--nfd-onboarding-drawer-bg);
    border-radius: 0;
    display: flex;
    position: absolute;
    z-index: z-index(".edit-site-navigation-toggle");
    height: $header-height;
    width: $header-height;
    &-button {
        align-items: center;
        background: var(--nfd-onboarding-drawer-bg);
        border-radius: 0;
        color: var(--nfd-onboarding-drawer-fg);
        height: $header-height + $border-width;
        width: $header-height;
        z-index: 1;
        margin-bottom: - $border-width;
        &.has-icon {
            min-width: $header-height;

            &:hover,
            &:active {
                color: var(--nfd-onboarding-drawer-fg);
            }

            &.is-suppressed{
                cursor: default;
            }

            &:focus {
                box-shadow: none;
            }

            &::before {
                transition: box-shadow 100ms ease;
                @include reduce-motion("transition");
                content: "";
                display: block;
                position: absolute;
                top: 9px;
                right: 9px;
                bottom: 9px;
                left: 9px;
                border-radius: $radius-block-ui + $border-width + $border-width;
                box-shadow: inset 0 0 0 var(--wp-admin-border-width-focus) var(--nfd-onboarding-drawer-bg);
            }

            &:hover::before {
                box-shadow: inset 0 0 0 var(--wp-admin-border-width-focus) $gray-700;
            }

            &:focus::before {
                box-shadow: inset 0 0 0 var(--wp-admin-border-width-focus) rgba(var(--nfd-onboarding-drawer-fg), 0.1);
            }
        }
    }
}

.nfd-onboarding-drawer__panel {
    height: 100%;
    position: relative;
    width: 0;
    overflow: auto;
    background: var(--nfd-onboarding-drawer-bg);
    color: var(--nfd-onboarding-drawer-fg);
    transition: width 100ms linear;
    @include reduce-motion("transition");

    .interface-interface-skeleton.has-footer & {
        @include break-medium() {
            height: calc(100% - #{$button-size-small + $border-width});
        }
    }

    &-inner {
        position: relative;
        width: $nav-sidebar-width;
        height: 100%;
        overflow: auto;
        visibility: hidden;
        transition: visibility 100ms linear;
        @include reduce-motion("transition");
    }

    &-site-title-container {
        height: $header-height;
        padding-left: $header-height;
        margin: 0 $grid-unit-20 0 $grid-unit-10;
        display: flex;
        align-items: center;
    }

    &-site-title {
        font-weight: 600;
        font-size: 14px;
        line-height: 20px;
        color: $gray-300;

        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
    }

    &-scroll-container {
        overflow-x: hidden;
        overflow-y: auto;
        height: calc(100% - #{$header-height});
    }

    &-inside {
        padding: 0 $grid-unit-20;
        color: var(--nfd-onboarding-drawer-fg);
        h2 {
            color: var(--nfd-onboarding-drawer-fg);
        }
    }

    .components-button.nfd-onboarding-drawer__panel-back.is-tertiary {
        color: var(--nfd-onboarding-drawer-fg);
        opacity: 0.7;
        height: $button-size;
        margin-top: $grid-unit-30;
        padding: $grid-unit $grid-unit-20 $grid-unit $grid-unit;

        svg {
            margin-right: 0;
        }

        &:hover:not(:disabled),
        &:focus:not(:disabled) {
            opacity: 1;
            box-shadow: none;
        }

        &:active {
            background-color: transparent;
        }
    }

    &-menu {
        margin: $grid-unit-30 0;
        h3 {
            color: var(--nfd-onboarding-drawer-fg);
            font-size: 20.28px;
            line-height: 1.2;
            padding: $grid-unit-05 $grid-unit-10 $grid-unit-05 $grid-unit-20;
            margin-top: 0;
            height: $grid-unit-50;
            max-height: $grid-unit-60;
            display: flex;
            align-items: center;
            margin-bottom: $grid-unit-10;
        }
    }

    &-menu-item {
        display: block;
        width: 100%;
        color: var(--nfd-onboarding-drawer-fg);
        font-size: 14px;
        line-height: 20px;
    }

    &-menu-link {
        display: flex;
        padding: $grid-unit-10 $grid-unit-20;
        text-decoration: none;
        color: var(--nfd-onboarding-light);
        align-items: center;
        border-radius: 2px;
        transition: background-color 100ms linear;
        @include reduce-motion("transition");
        svg {
            fill: var(--nfd-onboarding-drawer-icon-fill);
            transition: fill 100ms linear;
            @include reduce-motion("transition");
            margin-right: $grid-unit-10;
        }
        &:focus {
            box-shadow: 0 0 0 var(--wp-admin-border-width-focus) var(--wp-admin-theme-color);
            outline: 3px solid transparent;
            color: var(--nfd-onboarding-drawer-fg);
        }
        &:hover {
            color: var(--nfd-onboarding-drawer-fg);
        }
        &.active {
            background-color: var(--nfd-onboarding-primary);
            color: var(--nfd-onboarding-light);
            svg {
                fill: var(--nfd-onboarding-drawer-icon-active-fill);
            }
        }
    }
}

.nfd-onboarding-drawer__panel.is-open {
    width: $nav-sidebar-width;
    .nfd-onboarding-drawer__panel-inner {
        visibility: visible;
    }
}

.is-drawer-fade {
    animation-name: fadeIn;
    animation-duration: 100ms;
    animation-timing-function: ease-in;
}

.theme-styles-preview__list--drawer {
    width: $nav-sidebar-width -30px;
    height: 100%;
    align-items: center;
    justify-content: center;
}